import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import "../../style.css";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";
import pdf from "../../Assets/Akshay_Resume.pdf";

function Resume() {
	const uri = "https://porfolio-backend.vercel.app/ranks/getRanks";
	const [spojRank, upadteSpojRank] = useState(0);
	const [hackerrank, upadteHackerank] = useState(0);
	const [sem, upadateSem] = useState(0);
	const [cgpa, upadteCgpa] = useState(0);

	useEffect(() => {
		axios
			.get(uri)
			.then((res) => {
				upadteSpojRank(res.data.message[0].spojRank);
				upadteHackerank(res.data.message[1].hackerrank);
				upadteCgpa(res.data.message[2].cgpa);
				upadateSem(res.data.message[3].sem);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	return (
		<Container fluid className='resume-section'>
			<Particle />
			<Container>
				<Row style={{ justifyContent: "center", position: "relative" }}>
					<Button variant='primary' href={pdf} target='_blank'>
						<i className='fas fa-download'>&nbsp;</i>Download CV
					</Button>
				</Row>
				<Row className='resume'>
					<Col md={6} className='resume-left'>
						<h3 className='resume-title'>Experience</h3>
						<Resumecontent
							title='Associate Software Developer [Altimetrik]'
							date='June 2019 - Present'
							content={[
								"Built, tested, deployed scalable, highly available, and modular software products.",
								"Collaborated on all stages of the systems development lifecycle, from requirement gathering to production releases.",
								"Discussed project progress with customers, collected feedback on different stages and directly addressed concerns.",
							]}
						/>
						<h3 className='resume-title'>Internship</h3>
						<Resumecontent
							title='Backend Java Developer'
							content={[
								"Worked on the Fullstack project using Java, JavaScript, Angular 7.",
								"Developed designs to meet specific requirements such as quick-loading sites.",
							]}
						/>
					</Col>
					<Col md={6} className='resume-right'>
						<h3 className='resume-title'>Education</h3>
						<Resumecontent
							title='Dr. D.Y. Patil College OF Engineering  '
							date='2015 - 2019'
							content={[`B.E: Information Technology`, `CGPA: 8.04`]}
						/>
						<Resumecontent
							title='12TH BOARD [Shivam Internation School]'
							date='2012 - 2014'
							content={["Precentage: 88.4%"]}
						/>
						<Resumecontent
							title='10TH BOARD [A.V.N English School] '
							date='2000 - 2012'
							content={["CGPA: 9.4"]}
						/>
					</Col>
				</Row>
				<Row style={{ justifyContent: "center", position: "relative" }}>
					<Button variant='primary' href={pdf} target='_blank'>
						<i className='fas fa-download'>&nbsp;</i>Download CV
					</Button>
				</Row>
			</Container>
		</Container>
	);
}

export default Resume;
